
.Field--bd {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.Field--label {
  min-width: 75px;
  max-width: 150px;
  height: 20px;
  padding-right: 10px;
  font-size: 15px;
  line-height: 20px;
  font-family: sans-serif;
  color: #333;
}
.Field--input {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 20px;
  font-size: 0;
}
.Field--input input {
  -webkit-appearance: none;
  outline: none;
  width: 100%;
  height: 20px;
  padding: 0;
  border: 0;
  border-radius: 0;
  font-size: 15px;
  line-height: 20px;
  font-family: sans-serif;
  background-color: #fff;
}
.Field--delBox {
  padding-left: 5px;
}
.Field--del {
  position: relative;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: #d9d9d9;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.Field--del:before {
  content: "";
  width: 9px;
  height: 1px;
  position: absolute;
  left: 3px;
  right: 3px;
  top: 7px;
  bottom: 7px;
  background-color: #fff;
}
.Field--del:after {
  content: "";
  width: 1px;
  height: 9px;
  position: absolute;
  top: 3px;
  bottom: 3px;
  left: 7px;
  right: 7px;
  background-color: #fff;
}


