
.RadioOption {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  min-height: 28px;
  padding-top: 10px;
  padding-bottom: 10px;
}
.RadioOption:before {
  border-top: 1px solid #e5e5e5 !important;
}
.RadioOptionBox:first-child .RadioOption:before {
  border-top: none !important;
}
.RadioOption--icon {
  display: inline-block;
  overflow: hidden;
  width: 20px;
  height: 20px;
  border: 1px solid #d9d9d9;
  border-radius: 50%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-color: #fff;
}
.RadioOption--iconInner {
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  -webkit-transition: all 0.15s;
  transition: all 0.15s;
  background-color: #fff;
}
.RadioOption.active .RadioOption--icon {
  border-color: #2b8ff7;
  background-color: #2b8ff7;
}
.RadioOption.active .RadioOption--iconInner {
  -webkit-transform: scale(0.35);
          transform: scale(0.35);
}
.RadioOption--text {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  margin-left: 10px;
  font-size: 15px;
  color: #333;
}
.RadioOption--ipt {
  padding-bottom: 10px;
  padding-right: 10px;
}
.RadioOption--ipt input {
  outline: none;
  width: 100%;
  height: 28px;
  padding: 7px 3px;
  border: 1px solid #e5e5e5;
  border-radius: 2px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 12px;
  line-height: 1;
  background-color: #f5f5f5;
}


