
.TabBarItem {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 48px;
  line-height: 1;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  color: #757575;
  font-size: 13px;
}
.TabBarItem--title {
  display: block;
  height: 13px;
}
.TabBarItem--icon {
  height: 22px;
  padding-bottom: 2px;
  text-align: center;
}
.TabBarItem--icon img {
  height: 100%;
}
.TabBarItem.selected {
  color: #2b8ff7;
}


