
.TabBar {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.TabBar--fixed {
  position: fixed;
}
.TabBar--hide {
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
}


