
.Toast {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 100%;
}
.Toast--main {
  max-width: 195px;
  padding: 10px;
  border-radius: 5px;
  font-size: 13px;
  line-height: 1.5;
  color: #fff;
  background-color: rgba(0,0,0,0.7);
}
.Toast--main.bottom {
  position: absolute;
  bottom: 80px;
}
.Toast--main.center {
  -ms-flex-item-align: center;
      align-self: center;
}
.Toast--main.top {
  position: absolute;
  top: 80px;
}


