
.Dialog {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100%;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.Dialog--main {
  border-radius: 4px;
  background-color: #fff;
}
.Dialog--bd {
  padding: 15px;
}
.Dialog--title {
  margin: 0;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
  text-align: center;
  color: #000;
}
.Dialog--content {
  text-align: center;
}
.Dialog--text {
  display: inline-block;
  text-align: left;
  font-size: 15px;
  line-height: 1.5;
  color: #757575;
}
.Dialog--button {
  position: relative;
  display: inline-block;
  width: 100%;
  height: 40px;
  padding: 0;
  border: none;
  font-size: 15px;
  line-height: 40px;
  text-align: center;
  outline: none;
  color: #2b8ff7;
  background-color: transparent;
  -webkit-user-select: none;
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
  text-decoration: none;
}
.Dialog--separator {
  position: relative;
}
.Dialog--separator:after {
  position: absolute;
  left: 50%;
  content: "";
  width: 1px;
  height: 100%;
  border-left: 1px solid #ccc;
  -webkit-transform: scaleX(0.5) translateX(-50%);
          transform: scaleX(0.5) translateX(-50%);
}
.Dialog--prompt {
  outline: none;
  -webkit-appearance: none;
  width: 100%;
  height: 24px;
  padding: 5px 3px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 1px solid #d9d9d9;
  border-radius: 3px;
  font-size: 14px;
  line-height: 1;
}


