
.Datepicker {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  position: relative;
  height: 26px;
}
.Datepicker input {
  outline: none;
  position: absolute;
  top: 0;
  bottom: 0;
  z-index: 2;
  width: 100%;
  height: 100%;
  border: none;
  padding: 0;
  color: #fff;
  font-size: 0;
  background: transparent;
}
.Datepicker--bd {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 26px;
  line-height: 26px;
  font-size: 14px;
  text-align: right;
}


