
.Loading {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.Loading--main {
  padding: 10px;
  border-radius: 5px;
  line-height: 0;
  text-align: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-color: rgba(0,0,0,0.6);
}
.Loading--message {
  margin: 0;
  padding-top: 10px;
  font-size: 12px;
  line-height: 1;
  color: #fff;
}


