
.Switcher {
  position: relative;
  width: 60px;
  height: 30px;
  padding: 2px;
  border-radius: 18px;
  background-color: #d5d5d5;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
}
.Switcher--slider {
  position: absolute;
  top: 2px;
  left: 2px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: #fff;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
}
.Switcher--text {
  float: left;
  width: 50%;
  text-align: center;
  line-height: 30px;
  font-size: 14px;
  color: #fff;
}
.Switcher--open {
  -webkit-transform: translateX(30px);
          transform: translateX(30px);
}


