
.CellBox {
  background-color: #fff;
}
.Cell--active:active {
  background-color: #f5f5f5;
}
.Cell {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  min-height: 48px;
  padding-top: 8px;
  padding-bottom: 8px;
  padding-left: 15px;
  padding-right: 15px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.Cell--bd {
  padding-left: 5px;
  overflow: hidden;
  word-break: break-all;
  font-size: 15px;
  color: #8e8e90;
}
.Cell--hd {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.Cell--title {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  font-size: 15px;
  color: #333;
}
.Cell--icon {
  width: 22px;
  height: 22px;
  padding-right: 8px;
  line-height: 1;
  overflow: hidden;
}
.Cell--icon > * {
  width: 100%;
  height: 100%;
}
.Cell--arrow {
  display: block;
  width: 10px;
  height: 10px;
  border-top-color: #ccc;
  border-right-color: #ccc;
  border-top-style: solid;
  border-right-style: solid;
  border-top-width: 1px;
  border-right-width: 1px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}


